'use client'

import { User } from '@supabase/supabase-js'
import { useState, useEffect } from 'react'
import { useAuth } from '../context/auth'
import { supabase } from '../lib/supabase'
import toast from 'react-hot-toast'

interface DashboardProps {
  user: User
}

interface Recommendation {
  id: string
  link: {
    url: string
    title: string
    description: string
    image_url: string
  }
  from_user: {
    email: string
  }
  note: string
  created_at: string
  status: 'unread' | 'read' | 'dismissed'
}

interface RawRecommendation {
  id: string
  link: {
    url: string
    title: string | null
    description: string | null
    image_url: string | null
  }
  from_user: {
    email: string
  }
  note: string | null
  created_at: string
  status: 'unread' | 'read' | 'dismissed'
}

export default function Dashboard({ user }: DashboardProps) {
  const [url, setUrl] = useState('')
  const [loading, setLoading] = useState(false)
  const [recommendations, setRecommendations] = useState<Recommendation[]>([])
  const { signOut } = useAuth()

  useEffect(() => {
    fetchRecommendations()
  }, [])

  const fetchRecommendations = async () => {
    try {
      const { data, error } = await supabase
        .from('recommendations')
        .select(`
          id,
          link:links (
            url,
            title,
            description,
            image_url
          ),
          from_user:profiles!recommendations_from_user_id_fkey (
            email
          ),
          note,
          created_at,
          status
        `)
        .eq('to_user_id', user.id)
        .order('created_at', { ascending: false })

      if (error) throw error

      // Transform the data to match our Recommendation type
      const transformedData = (data as unknown as RawRecommendation[]).map(rec => ({
        id: rec.id,
        link: {
          url: rec.link.url,
          title: rec.link.title || '',
          description: rec.link.description || '',
          image_url: rec.link.image_url || ''
        },
        from_user: {
          email: rec.from_user.email
        },
        note: rec.note || '',
        created_at: rec.created_at,
        status: rec.status
      }))

      setRecommendations(transformedData)
    } catch (error) {
      console.error('Error fetching recommendations:', error)
      toast.error('Failed to load recommendations')
    }
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)

    try {
      // First, create the link
      const { data: link, error: linkError } = await supabase
        .from('links')
        .insert({
          url,
          created_by: user.id
        })
        .select()
        .single()

      if (linkError) throw linkError

      // Then, create the recommendation
      const { error: recommendationError } = await supabase
        .from('recommendations')
        .insert({
          link_id: link.id,
          from_user_id: user.id,
          to_user_id: user.id, // For now, recommending to self
          status: 'unread'
        })

      if (recommendationError) throw recommendationError

      toast.success('Link added successfully!')
      setUrl('')
      fetchRecommendations()
    } catch (error) {
      console.error('Error adding link:', error)
      toast.error('Failed to add link')
    } finally {
      setLoading(false)
    }
  }

  const handleStatusChange = async (recommendationId: string, newStatus: 'read' | 'dismissed') => {
    try {
      const { error } = await supabase
        .from('recommendations')
        .update({ status: newStatus })
        .eq('id', recommendationId)

      if (error) throw error

      setRecommendations(recommendations.map(rec => 
        rec.id === recommendationId ? { ...rec, status: newStatus } : rec
      ))
    } catch (error) {
      console.error('Error updating status:', error)
      toast.error('Failed to update status')
    }
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex justify-between items-center mb-8">
        <h1 className="text-2xl font-bold">Welcome, {user.email}</h1>
        <button
          onClick={() => signOut()}
          className="px-4 py-2 text-sm text-gray-600 hover:text-gray-800"
        >
          Sign Out
        </button>
      </div>

      <div className="max-w-2xl mx-auto">
        <form onSubmit={handleSubmit} className="mb-8">
          <div className="flex gap-2">
            <input
              type="url"
              value={url}
              onChange={(e) => setUrl(e.target.value)}
              placeholder="Enter a URL to share..."
              className="flex-1 rounded-md border-gray-300 shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
              required
            />
            <button
              type="submit"
              disabled={loading}
              className="px-4 py-2 bg-indigo-600 text-white rounded-md hover:bg-indigo-500 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 disabled:opacity-50"
            >
              {loading ? 'Adding...' : 'Share'}
            </button>
          </div>
        </form>

        <div className="space-y-4">
          {recommendations.length === 0 ? (
            <p className="text-gray-500 text-center">No recommendations yet</p>
          ) : (
            recommendations.map((rec) => (
              <div
                key={rec.id}
                className="bg-white rounded-lg shadow p-4 hover:shadow-md transition-shadow"
              >
                <div className="flex justify-between items-start mb-2">
                  <div>
                    <h3 className="font-medium">
                      <a
                        href={rec.link.url}
                        target="_blank"
                        rel="noopener noreferrer"
                        className="text-indigo-600 hover:text-indigo-500"
                      >
                        {rec.link.title || rec.link.url}
                      </a>
                    </h3>
                    <p className="text-sm text-gray-500">
                      From: {rec.from_user.email}
                    </p>
                  </div>
                  <div className="flex gap-2">
                    {rec.status === 'unread' && (
                      <>
                        <button
                          onClick={() => handleStatusChange(rec.id, 'read')}
                          className="text-sm text-green-600 hover:text-green-500"
                        >
                          Mark as Read
                        </button>
                        <button
                          onClick={() => handleStatusChange(rec.id, 'dismissed')}
                          className="text-sm text-red-600 hover:text-red-500"
                        >
                          Dismiss
                        </button>
                      </>
                    )}
                  </div>
                </div>
                {rec.note && (
                  <p className="text-gray-600 text-sm mt-2">{rec.note}</p>
                )}
                {rec.link.description && (
                  <p className="text-gray-500 text-sm mt-2">
                    {rec.link.description}
                  </p>
                )}
              </div>
            ))
          )}
        </div>
      </div>
    </div>
  )
} 